<template>
  <div class="flow-send-list">
    <el-table :data="tableData" style="width: 100%;" @row-click="goDetail">
      <el-table-column
        prop="objectname"
        label="文件标题"
        width="180"
      ></el-table-column>
      <el-table-column
        prop="nodename"
        label="文件类型"
        width="180"
      ></el-table-column>
      <el-table-column prop="sendusername" label="发件人"></el-table-column>
      <el-table-column prop="reachtime" label="发件时间"></el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "FlowTemp",
  data() {
    return {
      tableData: [],
    };
  },
  created() {
    this.initTable();
  },
  mounted() {},
  methods: {
    initTable() {
      const param = {
        iCurPage: 0,
        iPageNum: 5,
      };
      this.$http
        .get(dsf.url.getWebPath("wfr/getTodoList"), param)
        .then((res) => {
          if (res.data.success && res.data.data) {
            this.tableData = res.data.data;
          } else {
            dsf.layer.message(res.data.Message || "请求异常", false);
          }
        })
        .catch((res) => {
          dsf.layer.message("请求异常", false);
        });
    },
    goDetail(row) {
      // const url = `/wfp/page?sID=${row.objectid}&sPID=${row.pid}&sCurLinkID=${row.linkid}&sWFID=${row.wfid}&sNodeID=${row.nodeid}`;
      this.$http
        .get(dsf.url.getWebPath("/wfp/getPageUrl"), {
          sID: row.objectid,
          sPID: row.pid,
          sCurLinkID: row.linkid,
          sWFID: row.wfid,
          sNodeID: row.nodeid,
        })
        .then((res) => {
          if (res.data.success && res.data.data) {
            this.$openWindow(dsf.url.getWebPath("~" + res.data.data));
          } else {
            dsf.layer.message(res.data.Message || "请求异常", false);
          }
        })
        .catch((res) => {
          dsf.layer.message("请求异常", false);
        });
      // this.$openWindow(url);
    },
  },
};
</script>
<style lang="scss" scoped></style>
